body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

body {
  background-color: #000000;
  color: #555555;
}
h4 {
  font-family: sans-serif;
  color: #555555;
  font-size: 16px;
}
h3 {
  font-family: sans-serif;
  color: #555555;
}
p {
  font-family: sans-serif;
  color: #555555;
  font-size: 14px;
}
a {
  font-family: sans-serif;
  color: #d15423;
  text-decoration: none;
}

#displayCanvas {
  z-index: 0;
}

#displayCanvas:-webkit-full-screen {
  width: 100%;
  height: 100%;
}

#container {
  width: 100%;
  height: 100%;
  margin: auto;
}

.color-automata .dg .property-name {
  width: 50%;
}

.color-automata .dg .boolean .property-name {
  width: 100%;
}

.color-automata .dg .c {
  width: 50%;
}



